<template>
	<div id="mall-news-detail">
		<public-header :title="news.title"></public-header>
		<div class="content" v-html="news.content"></div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				preLoading:false,
				id: Number,
				news: {},
			}
		},
		components: {
			PublicHeader
		},
		methods: {
			onLoad() {
				this.preLoading = true;
				this.util.request({
					url: 'wmall/home/news/detail',
					data: {
						id: this.id,
					}
				}).then((res) => {
					this.preLoading = false;
					var result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					this.news = result.message.news;
				})
			}
		},
		created() {
			if(this.$route.query.id) {
				this.id = this.$route.query.id;
			}
			if(this.$route.query.type) {
				this.type = this.$route.query.type;
			}

		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style scoped>
	#mall-news-detail .content{
		background-color: #fff;
		padding: 10px;
		line-height: 1.5;
	}
</style>


